.loginContainer {
  justify-content: center;
  padding-top: 120px;
  background-image: url(/static/media/waffles.b7faf746.jpg) !important;
  background-repeat: no-repeat !important;
  background-size: cover;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 100vh;
  min-height: 250px;
}
