/* footer styling and positioning */
.footer {
    position: fixed;
    background-color: #2b6a4d;
    width: 100%;
    padding: 10px;
    bottom: 0;
    text-align: center;
    color: white;
  }

  a:hover {
    color: #9d0208 !important;
  }

